.media{
    display: flex;
    flex-direction: column;
    width: 200px;
    padding: 5px;
    margin:  8px 0;
    background-color: #282c34;
    border-radius: 10px;
    position: relative;
    font-family: "Montserrat", sans-serif;
    
}

.media:hover{
    background-color: white;
    color: black;
    width: 210px;
    
}

@media (max-width: 550px){
    .media{
        width: 46%;
    }
}
.sunTitle{
    display: flex;
    justify-content: space-between;
    padding-bottom: 3px;
    padding: 0 2px;
}
.title{
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
}
.poster{
    border-radius: 10px;
}




